@font-face {
  font-family: Geo;
  font-style: normal;
  src: url("../fonts/__myfont__.ttf");
}
html {
  color: #000;
  background: #fff;
}
html,
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
textarea,
p,
blockquote,
th,
td,
figure {
  margin: 0;
  padding: 0;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
fieldset,
img {
  border: 0;
}
li {
  list-style: none;
}
caption,
th {
  text-align: left;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
}
abbr,
acronym {
  border: 0;
  font-variant: normal;
}
sup {
  vertical-align: text-top;
}
sub {
  vertical-align: text-bottom;
}
input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}
input,
textarea,
select {
  *font-size: 100%;
}
legend {
  color: #000;
}
a img {
  border: none;
}
blockquote,
q {
  quotes: none;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}
.clear {
  margin: 0 0 -1px 0;
  line-height: 1px;
  display: block;
  font-size: 1px;
  clear: both;
  height: 1px;
}
.clearfix:after {
  visibility: hidden;
  display: block;
  line-height: 0;
  content: " ";
  clear: both;
  height: 0;
}
* html .clearfix {
  zoom: 1;
}
*:first-child+html .clearfix {
  zoom: 1;
}
html,
body {
  font-family: Verdana, Geneva, sans-serif;
  font-size: 14px;
  line-height: 1.2;
  color: #000;
}
h1 {
  font-size: 2.3em;
}
h2 {
  font-size: 1.8em;
}
h3 {
  font-size: 1.4em;
}
h4 {
  font-size: 1.2em;
}
h5 {
  font-size: 1.1em;
}
h6 {
  font-size: 1em;
  font-weight: bold;
}
p {
  font-size: 1em;
  margin-bottom: 1em;
}
b,
strong {
  font-weight: bold;
}
a {
  color: #888;
  text-decoration: underline;
  outline: none;
}
a:hover,
a:focus {
  color: #000;
  text-decoration: underline;
}
ul,
ol {
  margin: 0 0 1em;
  padding: 0 0 0 25px;
}
ul li,
ol li {
  margin: 0;
  padding: 0;
}
ul li {
  list-style: disc;
}
ol li {
  list-style: decimal;
}
html,
body {
  min-height: 100%;
}
.loginWrapper {
  padding: 15px 15px 0px 15px;
  border: solid #808080 2px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
  margin-top: 50%;
}
.loginWrapper img {
  margin-bottom: 15px;
}
